<template>
  <section class="mb-5">
    <div class="py-2 box mb-0 has-background-success is-size-7" v-if="hasConfirmedVaccinationNotice">
      <p class="has-text-white">
        <i class="fas fa-check fa-fw"></i>&nbsp;{{$t('profile.vaccination.completed')}}
      </p>
    </div>
    <div class="py-2 box mb-0 has-background-warning is-size-7" v-else>
      <p class="has-text-black">
        <i class="fas fa-arrow-down fa-fw"></i>&nbsp;{{$t('profile.vaccination.notCompleted')}}
      </p>
    </div>
    <div class="box">
    <div class="is-flex is-flex-direction-row is-justify-content-space-between is-clickable" @click="isOpened = !isOpened">
      <p class="is-size-4 is-700">
        {{$t('profile.vaccination.title')}}<span v-if="hasConfirmedVaccinationNotice">&nbsp;&nbsp;<i class="fas fa-check-circle has-text-success fa-fw"></i></span>
      </p>
      <p class="is-size-4">
        <i class="fas" :class="isOpened ? 'fa-angle-up' : 'fa-angle-down'"></i>
      </p>
    </div>
    <div v-show="isOpened">
    <br>
    <div class="content" v-html="$t('profile.vaccination.contentAbout')" />
    <div class="box mb-0" :class="{'has-background-warning': !hasAlreadyDeclared}">
      <p class="is-size-4 is-700 mb-4">
        {{$t('profile.vaccination.completeSchemaTitle')}}<span v-if="hasConfirmedVaccinationNotice">&nbsp;&nbsp;<i class="fas fa-check-circle has-text-success fa-fw"></i></span>
      </p>
      <div class="content">
        <p>
          <label class="checkbox">
          <input v-model="term1" :disabled="hasAlreadyDeclared"  type="checkbox">
            {{$t('profile.vaccination.term1')}}
          </label>
        </p>
        <p>
          <label class="checkbox">
          <input v-model="term2" :disabled="hasAlreadyDeclared"  type="checkbox">
            {{$t('profile.vaccination.term2')}}
          </label>
        </p>
        <p>
          <label class="checkbox">
          <input v-model="term3" :disabled="hasAlreadyDeclared"  type="checkbox">
            {{$t('profile.vaccination.term3')}}
          </label>
        </p>
      </div>
      
      <div class="buttons is-right" v-if="!hasAlreadyDeclared">
        <button @click="submit" :disabled="!term1 || !term2 || !term3" class="button is-primary" target="_blank">
          <i class="fas fa-check"></i>
          &nbsp;{{$t('profile.vaccination.confirmButton')}}
        </button>
      </div>
      </div>
    </div>
    </div>
  </section>
</template>

<script>
import userUtils from "@/mixins/userUtils";
// import TakeAction from "@/components/profile/TakeAction";

export default {
  data() {
    return {
      isOpened: true,
      //data
      term1: false,
      term2: false,
      term3: false,
      confirmVaccinationNotice: false,
    };
  },
  created() {
    this.sync();
  },
  mixins: [userUtils],
  components: {
    // TakeAction
  },
  methods: {
    doLogout: function () {
      // REQUIRED
      this.$http.post('/user/logout')
        .then(() => {
          window.location.href = '/';
        })
        .catch((err) => {
          console.error(err)
          this.$swal.fire({
            icon: 'error',
            title: 'Oops..',
            text: 'Something happened'
          })
        })
    },
    sync: function() {
      this.confirmVaccinationNotice = this.user.confirmVaccinationNotice;
      if(this.user.confirmVaccinationNotice){
        this.hasAlreadyDeclared = true;
        this.term1 = true;
        this.term2 = true;
        this.term3 = true;
      }
    },
    async submit() {
      this.startLoading()
      const payload = {
        confirmVaccinationNotice: true
      };
      this.$http.post("/user/update", payload)
        .then(() => this.updateUser())
        .then(() => this.sync())
        .then(() => {
          this.$swal.fire({
            icon: 'success',
            title: this.$t('profile.alertSuccess.title'),
            text: this.$t('profile.alertSuccess.text')
          })
        })
        .catch(err => {
          console.error(err);
          this.$swal.fire({
            icon: 'error',
            title: this.$t('profile.alertError.title'),
            text: this.$t('profile.alertError.text')
          })
        })
        .finally(() => {
          this.stopLoading()
        });
    }
  }
};
</script>