<template>
  <section>
  <div class="box">
     <div class="is-flex is-flex-direction-row is-justify-content-space-between">
        <h1 class="title is-4">{{$t('profile.takeAction.recieve.title')}}</h1>
        <a @click="$emit('exit')" class="is-pulled-right"><i class="fas fa-chevron-left fa-fw"></i>&nbsp;{{$t('profile.takeAction.goBack')}}</a>
      </div>
      <div class="content">
        <p v-for="(p,i) in $t('profile.takeAction.recieve.content')" :class="{'add-margin': p.type === 'li'}" v-html="p.content" :key="i"></p>
        <p><a href="https://new.furfiesta.com/reglamentos/" class="has-text-info">{{$t('profile.takeAction.linkRules')}}</a></p>
      </div>
      <h1 class="title is-3 has-text-centered">{{$t('profile.takeAction.recieve.yourToken')}}</h1>
      <h1 class="subtitle is-5 has-text-centered">{{$t('profile.takeAction.recieve.giveIt')}}</h1>
      <br>
      <div class="section has-text-centered" v-if="isLoading">
        <i class="fas fa-spinner fa-spin fa-4x"></i>
      </div>
      <div class="has-text-centered" v-else>
        <p class="is-size-2 emoji-pic">{{theToken.token.slice(0,8)}}</p>
        <p class="is-size-2 emoji-pic">{{theToken.token.slice(8,16)}}</p>
        <p class="is-size-2 emoji-pic">{{theToken.token.slice(16,24)}}</p>
        <p class="is-size-2 emoji-pic">{{theToken.token.slice(24,32)}}</p>
        <br>
        <div class="columns is-centered" v-if="!cannot">
          <div class="column is-half">
            <div class="buttons is-centered">
              <button @click="copyEmoji" class="button is-fullwidth"><span class="spaced"><i class="far fa-clipboard fa-fw"></i>&nbsp;{{$t('profile.takeAction.recieve.copyToken')}}</span></button>
              <button @click="revokeToken" class="button is-link is-fullwidth"><span class="spaced"><i class="fas fa-redo-alt fa-fw"></i>&nbsp;{{$t('profile.takeAction.recieve.generateNewToken')}}</span></button>
            </div>
          </div>
        </div>
        <p class="is-size-7">{{$t('profile.takeAction.recieve.problems',{token: theToken.ascii_token})}}</p>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data(){
    return {
      theToken: {
        token: '',
        ascii_token: 'CANNOT CANNOT CANNOT CANNOT'
      },
      isLoading: true,
      cannot: false,
      revokedToken: false
    }
  },
  mounted: function(){
    this.getToken()
  },
  methods:{
    copyEmoji: function(){
       this.$copyText(this.theToken.token).then(e => {
          this.$swal.fire({
            icon: 'success',
            title: 'OK ',
            text: this.$t('profile.takeAction.recieve.copySuccess')
          })
          console.log(e)
        }, e => {
          this.$swal.fire({
            icon: 'warning',
            title: 'OH oh..',
            text: this.$t('profile.takeAction.recieve.copyError')
          })
          console.log(e)
        })
    },
    getToken: function(){
      this.isLoading = true
      this.$http.get('/ticket/transfer/getToken')
      .then( res => {
        this.theToken = res.data
      })
      .catch( err => {
        console.error(err)
        this.$swal.fire({
            icon: 'error',
            title: this.$t('profile.alertError.title'),
            text: this.$t('profile.alertError.text')
          })
        this.cannot = true;
      })
      .finally( () => {
        this.isLoading = false
      })
    },
    revokeToken: function(){
      this.revokedToken = false
      this.isLoading = true
      this.$http.get('/ticket/transfer/newToken')
      .then( res => {
         this.$swal.fire({
            icon: 'success',
            title: 'OK ',
            text: this.$t('profile.takeAction.recieve.newTokenSuccess')
          })
        this.theToken = res.data
        
      })
      .finally( () => {
        this.isLoading = false
        this.revokedToken = true
      })
    }

  }
}
</script>

<style lang="scss" scoped>
.emoji-pic{
  letter-spacing: 20px;
  margin-left:20px;
  user-select: none;
}
.content a.thelink:hover {
  color: orange;
}
.add-margin {
  margin-left: 20px;
}
p {
  font-size: 0.9rem;
}
</style>