<template>
  <section>
  <div class="box">
    <div class="card-content">
      <div class="is-flex is-flex-direction-row is-justify-content-space-between">
        <h1 class="title is-4"><i class="fas fa-check-circle"></i> &nbsp;{{$t('profile.takeAction.confirm.title')}}</h1>
        <a @click="$emit('exit')" class="is-pulled-right"><i class="fas fa-chevron-left fa-fw"></i>&nbsp;{{$t('profile.takeAction.goBack')}}</a>
      </div>
      <div class="content">
      <div class="is-clearfix">
      </div>
        <p v-for="(p,i) in $t('profile.takeAction.confirm.content')" :class="{'add-margin': p.type === 'li'}" v-html="p.content" :key="i"></p>
        <!-- <p><router-link :to="{name: 'rules'}">{{$t('profile.takeAction.linkRules')}}</router-link></p> -->
      </div>
      <h1 class="title is-5 has-text-info">{{$t('profile.takeAction.confirm.confirmTitle')}}</h1>
      <div class="buttons">
        <button @click="confirm" class="button is-black"><span class="spaced"><i class="fas fa-check fa-fw"></i>&nbsp;{{$t('profile.takeAction.confirm.confirmButton')}}</span></button>
      </div>
    </div>
  </div>
  </section>
</template>

<script>
export default {
  methods: {
    confirm: function(){
      this.startLoading()
      this.$http.post('/ticket/confirmation/doConfirm')
      .then( () => { 
        this.$emit('update','confirm')
      })
      .catch( err => {
        this.stopLoading()
        console.error(err)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.content a.thelink:hover {
  color: orange;
}
.add-margin {
  margin-left: 20px;
}
p {
  font-size: 0.9rem;
}
</style>
