            </div>
            <div class="content my-4 has-text-centered">
            <p v-html="$t('profile.takeAction.donate.about')"></p>
            <p class="is-size-7">{{$t('profile.takeAction.donate.clickToRead')}}</p>
            </div>
            <div class="buttons is-centered">
              <button @click="toggleOption('donate')" :disabled="!hasCompletedPreInformation" class="button is-primary is-outlined is-centered">
                <span class="spaced">{{$t('profile.takeAction.continue')}} <i class="fas fa-arrow-right"></i></span>
              </button>
           </div>
        </section>
      </div>
      <div class="column is-6 is-flex is-flex-grow-1" v-if="canTransfer">
        <section class="box is-flex is-flex-direction-column is-justify-content-space-between is-flex-grow-1">
            <div>
            <p class="subtitle is-3 has-text-centered"><i class="fas fa-ticket-alt fa-fw"></i><i class="fas fa-angle-double-right fa-fw"></i></p>
            <h1 class="title is-5 has-text-centered">{{$t('profile.takeAction.transfer.title')}}</h1>
            </div>
            <div class="content my-4 has-text-centered">
            <p v-html="$t('profile.takeAction.transfer.about', {tier: user.ticket.tier})"></p>
            <p class="is-size-7">{{$t('profile.takeAction.transfer.clickToRead')}}</p>
            </div>
             <div class="buttons is-centered">
              <button @click="toggleOption('transfer')" :disabled="!hasCompletedPreInformation" class="button is-primary is-outlined is-centered">
