              </button>
            </div>
        </section>
      </div>
      <div class="column is-6 is-flex is-flex-grow-1" v-if="canRecieve">
        <section class="box is-flex is-flex-direction-column is-justify-content-space-between is-flex-grow-1">
            <div>
              <p class="subtitle is-3 has-text-centered"><i class="fas fa-angle-double-right fa-fw"></i><i class="fas fa-ticket-alt fa-fw"></i></p>
              <h1 class="title is-5 has-text-centered">{{$t('profile.takeAction.recieve.title')}}</h1>
            </div>
              <div class="content my-4 has-text-centered">
            <p v-html="$t('profile.takeAction.recieve.about')"></p>
            <p v-if="hasTicket" class="is-size-7 has-text-danger" v-html="$t('profile.takeAction.recieve.closure',{tier: user.ticket.tier})"></p>
            <p class="is-size-7">{{$t('profile.takeAction.recieve.clickToRead')}}</p>
            </div>
             <div class="buttons is-centered">
              <button @click="toggleOption('recieve')" :disabled="!hasCompletedPreInformation" class="button is-primary is-outlined is-centered">
                <span class="spaced">{{$t('profile.takeAction.continue')}} <i class="fas fa-arrow-right"></i></span>
              </button>
            </div>
        </section>
      </div>
      <div class="column" v-if="nothingToDo">
        <div class="box">
          <div>
            <p class="subtitle is-3 has-text-centered"><i class="fas fa-battery-empty fa-fw"></i></p>
            <h1 class="title is-5 has-text-centered">{{$t('profile.takeAction.nothingToDo.title')}}</h1>
          </div>
        </div>
      </div>
      <!-- <div class="column" v-if="canRollover">
        <section class="card">
          <div class="card-content">
            <p class="subtitle is-3 has-text-centered has-text-black"><i class="fas fa-lg fa-ticket-alt fa-fw"></i><i class="fas fa-lg fa-long-arrow-alt-right fa-fw"></i><i class="fas fa-lg fa-calendar-alt fa-fw"></i></p>
            <h1 class="title is-4 has-text-centered">{{$t('profile.takeAction.rollover.title')}}</h1>
