            <p class="is-size-5" v-html="$t('profile.takeAction.rollover.about')"></p>
            <p class="is-size-7">{{$t('profile.takeAction.rollover.clickToRead')}}</p>
            </div>
            <div class="buttons is-centered">
              <button @click="toggleOption('rollover')" class="button is-primary is-outlined is-centered">
