              </button>
            </div>
          </div>
        </section>
      </div> -->
      </div>
      <confirm v-if="showConfirm" @exit="toggleOption('choices')" @update="update"/>
      <donate v-if="showDonate" @exit="toggleOption('choices')" @update="update"/>
      <transfer v-if="showTransfer" @exit="toggleOption('choices')" @update="update"/>
      <recieve ref="receive" v-if="showRecieve" @exit="toggleOption('choices')" @update="update"/>
      <!-- <rollover v-if="showRollover" @exit="toggleOption('choices')" @update="update"/> -->
    </div>

  </section>
</template>

<script>
import Confirm from './action/Confirm.vue'
import Recieve from './action/Recieve.vue'
import Donate from './action/Donate.vue'
// import Rollover from './action/Rollover.vue'
import Transfer from './action/Transfer.vue'
import userUtils from "@/mixins/userUtils";

export default {
  props: ['enableUserFetch'],
  components:{
    Confirm,
    Recieve,
    Donate,
    // Rollover,
    Transfer
  },
  data() {
    return {
      isOpened: false,
      showChoices: true,
      allowTransfer: ['general','sponsor','supersponsor'],
      allowRollover: ['general','sponsor','supersponsor','awoo'],
      allowRecieve: ['general','sponsor', 'guest'],
      allowDonate: ['general','sponsor', 'supersponsor','awoo'],
      showConfirm: false,
      showRecieve: false,
      // showRollover: false,
      showDonate: false,
      showTransfer: false,
      intervalId: null,
    }
  },
  created() {
    if(this.enableUserFetch){
      this.intervalId = setInterval(this.getStatus, 10000);
    }
  },
  beforeDestroy() {
    if (this.intervalId) clearInterval(this.intervalId);
  },
  mixins: [userUtils],
  methods: {
    getStatus: function() {
      this.$http.get("/user").then(resUser => {
        if(this.showRecieve == true){
          if(resUser.data.ticket){
            if(this.user.ticket.tier != resUser.data.ticket.tier){
              this.$refs.receive.getToken()
              this.$swal.fire({
                icon: "success",
                title: this.$t('profile.takeAction.success'),
                text: this.$t('profile.takeAction.recieve.actionSuccess', {tier: resUser.data.ticket.tier}),
              });
              if(resUser.data.ticket.tier == 'supersponsor'){
                this.showRecieve = false;
                this.showChoices = true
              } 
            } 
          } else {
            if(this.user.ticket){
              this.$refs.receive.getToken()
            }
          }
        }
        this.$store.commit('updateUser', resUser.data)
      });
    },
    toggleOption: function(choice){
      switch(choice){
        case 'confirm': 
          this.showChoices = false
          this.showConfirm = true
          break;
        case 'recieve':
          this.showChoices = false
          this.showRecieve = true
          break;
        case 'donate':
          this.showChoices = false
          this.showDonate = true
          break;
        // case 'rollover':
        //   this.showChoices = false
        //   this.showRollover = true
        //   break;
        case 'transfer':
          this.showChoices = false
          this.showTransfer = true
          break;
        case 'choices':
          this.showChoices = true
          this.showConfirm = false
          this.showDonate = false
          this.showRecieve = false
          // this.showRollover = false
          this.showTransfer = false
      }
    },
    update: function(e){
      this.startLoading()
      this.$http
            .get("/user")
            .then(resUser => {
              let text = ''
              switch(e){
                case 'confirm':
                  text = this.$t('profile.takeAction.confirm.actionSuccess')
                  break;
                case 'donate':
                  text = this.$t('profile.takeAction.donate.actionSuccess')
                  break;
                case 'rollover':
                  text = this.$t('profile.takeAction.rollover.actionSuccess')
                  break;
                case 'transfer':
                  text = this.$t('profile.takeAction.transfer.actionSuccess')
                  break;
              }
              this.$swal.fire({
                icon: "success",
                title: this.$t('profile.takeAction.success'),
                text: text,
              });
              this.$store.commit('updateUser', resUser.data)
              this.toggleOption('choices')
            })
            .catch(errUser => {
              console.error(errUser);
              this.$swal.fire({
                icon: "error",
                title: this.$t('profile.takeAction.error'),
                text: this.$t('profile.takeAction.errorText'),
              });
            })
            .finally( () => {
              this.stopLoading()
            });
    }
  },
  computed: {
    canDonate: function(){
      if(this.hasTicket && this.allowDonate.includes(this.user.ticket.tier)) return true
      return false
    },
    canConfirm: function(){
      if(this.hasTicket && !this.hasConfirmedTicket) return true
      return false
    },
    canTransfer: function(){
      if(this.hasTicket && !this.hasConfirmedTicket && this.allowTransfer.includes(this.user.ticket.tier)) return true
      return false
    },
    canRecieve: function(){
      if(this.hasTicket  && this.allowRecieve.includes(this.user.ticket.tier)) return true
      if(!this.hasTicket) return true
      return false
    },
    nothingToDo: function(){
      // if every variable is false
      if(!this.canConfirm && !this.canDonate && !this.canRecieve && !this.canTransfer) return true
      return false
    }
    // canRollover: function(){
    //   if(this.hasTicket && this.allowRollover.includes(this.user.ticket.tier) && !this.user.rolloverTicket) return true
    //   return false
    // },
  }
}
</script>