<template>
  <section>

    <div class="box">
      <div class="is-flex is-flex-direction-row is-justify-content-space-between">
        <h1 class="title is-4">{{$t('profile.takeAction.transfer.title')}}</h1>
        <a @click="$emit('exit')" class="is-pulled-right"><i class="fas fa-chevron-left fa-fw"></i>&nbsp;{{$t('profile.takeAction.goBack')}}</a>
      </div>
      <div class="content">
      <p v-for="(p,i) in $t('profile.takeAction.transfer.content')" :class="{'add-margin': p.type === 'li'}" v-html="p.content" :key="i"></p>
        <p><a href="https://new.furfiesta.com/reglamentos/" class="has-text-info">{{$t('profile.takeAction.linkRules')}}</a></p>
      </div>
      <h1 class="title is-4">{{$t('profile.takeAction.transfer.confirmTitle')}}</h1>
      <h1
        class="subtitle is-6"
        v-html="$t('profile.takeAction.transfer.confirmSubtitle', {tier: user.ticket.tier})"></h1>
      <b-notification class="notification is-danger" @close="errorMessage = null" :active="errorMessage != null">
        <h1 class="title is-5">Error</h1>
        <p class="subtitle is-5">{{errorMessage}}</p>
      </b-notification>
      <ValidationObserver ref="obsaerver" v-slot="{ invalid }">
        <div class="field">
          <ValidationProvider
            name="Token"
            :rules="{required: true}"
            v-slot="{errors, classes}"
          >
            <input
              type="text"
              class="input is-medium"
              :class="classes"
              v-model="token"
              :placeholder="$t('form.required')"
            />
            <span class="help is-danger">{{ errors[0] }}</span>
          </ValidationProvider>
        </div>
      <div class="buttons is-right">
        <button @click="confirm" class="button is-primary" :disabled="invalid">
          <span class="spaced">
            <i class="fas fa-save fa-fw"></i>&nbsp;{{$t('profile.takeAction.transfer.confirmButton')}}
          </span>
        </button>
      </div>
      </ValidationObserver>
    </div>
  </section>
</template>

<script>
import userUtils from "@/mixins/userUtils";

export default {
  data() {
    return {
      token: null,
      errorMessage: null,
    };
  },
  mixins: [userUtils],
  methods: {
    confirm: function(){
      this.errorMessage = null
      this.startLoading()
      this.$http.post('/ticket/transfer/doTransfer', {
        token: this.token
      })
      .then(res => { 
        console.log(res)
        this.$emit('update','transfer')
      })
      .catch( err => {
        let errMessage = err.response.data.message
        switch (errMessage){
          case 'token not found':
            this.errorMessage = this.$t('profile.takeAction.transfer.errorMessage.tokenNotFound')
            break
          case 'destination user not found':
            this.errorMessage = this.$t('profile.takeAction.transfer.errorMessage.destinationNotFound')
            break
          case 'person already has same or better ticket':
            this.errorMessage = this.$t('profile.takeAction.transfer.errorMessage.sameOrBetterTicket')
            break
          case 'your ticket tier does not allow sending transfers':
            this.errorMessage = this.$t('profile.takeAction.transfer.errorMessage.notAllowed')
            break
          case 'user already has a confirmed ticket':
            this.errorMessage = this.$t('profile.takeAction.transfer.errorMessage.confirmed')
            break
          case 'transfer requires a successful ticket purchase"':
            this.errorMessage = this.$t('profile.takeAction.transfer.errorMessage.needsTicket')
            break
        }
        this.stopLoading()
        console.error(err)
      })
    }
  }
};
</script>

<style lang="scss" scoped>
.content a.thelink:hover {
  color: orange;
}
.add-margin {
  margin-left: 20px;
}
p {
  font-size: 0.9rem;
}
</style>